body{
  background: linear-gradient(65deg, #3D3D52, #343446);
}

.pad{
  height: 41em;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main{
  padding: 20px 30px 20px 30px;
  height: 400px;
  width: 760px;
  display: grid;
  grid-template-columns: 50px 50px 50px 50px;
  grid-template-rows: 130px 130px 130px 130px;
  grid-template-areas:
  "one two three space controls"
  "four five six space volume"
  "seven eight nine space breaks";
  background: linear-gradient(45deg, #4C4B63, #6D6977);
  grid-row-gap: 5px;
  grid-column-gap: 95px;
}

button{
  width: 140px;
  background: linear-gradient(85deg, #838386, #C6C6C6);
  font-size: 35px;
  outline: none;
}

.button1{grid-area: one;}
.button2{grid-area: two;}
.button3{grid-area: three;}
.button4{grid-area: four;}
.button5{grid-area: five;}
.button6{grid-area: six;}
.button7{grid-area: seven;}
.button8{grid-area: eight;}
.button9{grid-area: nine;}

.controls{grid-area: controls; position: relative; bottom: 25px;}

.volume{grid-area: volume;}

.bank{grid-area: breaks; position: relative; bottom: 8px;}

.display{
  background-color: #244D56;
  height: 60px;
  width: 200px;
  position: relative;
  right: 60px;
  display: table-cell;
  text-align: center;
  color: #EDEDBF;
  vertical-align: middle;
  font-weight: 500;
  font-size: 25px;
}

button:active {
 transform: translateY(10px) !important;
 background: linear-gradient(65deg, #9D3907, #F2BB05) !important;
}


/* Range Slider */
input[type="range"] {
    margin: auto;
    -webkit-appearance: none;
    position: relative;
    overflow: hidden;
    height: 40px;
    width: 300px;
    cursor: pointer;
    right: 120px;
    border-radius: 0; /* iOS */
    outline: none;
}

::-webkit-slider-runnable-track {
    background: linear-gradient(65deg, #62727F, #505D68);
}

::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 20px; /* 1 */
    height: 40px;
    background: linear-gradient(65deg, #12262B, #1E4048);
    box-shadow: -100vw 0 0 100vw dodgerblue;
    border: 2px solid #999; /* 1 */
}

::-moz-range-track {
    height: 40px;
    background: linear-gradient(65deg, #9D3907, #D74E09); /* Flame */
}

::-moz-range-thumb {
    background: linear-gradient(65deg, #12262B, #1E4048);
    height: 40px;
    width: 20px;
    border: 3px solid #999;
    border-radius: 0 !important;
    box-shadow: -100vw 0 0 100vw dodgerblue;
    box-sizing: border-box;
}

::-ms-fill-lower {
    background: dodgerblue;
}

::-ms-thumb {
    background: linear-gradient(65deg, #12262B, #1E4048);
    border: 2px solid #999;
    height: 40px;
    width: 20px;
    box-sizing: border-box;
}

::-ms-ticks-after {
    display: none;
}

::-ms-ticks-before {
    display: none;
}

::-ms-track {
    background: linear-gradient(65deg, #9D3907, #D74E09); /* Flame */
    color: transparent;
    height: 40px;
    border: none;
}

::-ms-tooltip {
    display: none;
}





/* Switch starts here */
.rocker {
  display: inline-block;
  position: relative;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  color: #888;
  width: 7em;
  height: 4em;
  overflow: hidden;
  border-bottom: 0.5em solid #4C4B63;
  bottom: 20px;
  right: 30px;
}

.rocker::before {
  content: "";
  position: absolute;
  top: 1em;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #999;
  border: 0.5em solid #4C4B63;
  border-bottom: 0;
}

.rocker input {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch-left,
.switch-right {
  cursor: pointer;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.5em;
  width: 3em;
  transition: 0.2s;
}

.switch-left {
  height: 2.4em;
  width: 2.75em;
  left: 0.85em;
  bottom: 0.4em;
  background: linear-gradient(65deg, #9D3907, #F2BB05);
  transform: rotate(15deg) skewX(15deg);
}

.switch-right {
  right: 0.5em;
  bottom: 0;
  background: linear-gradient(65deg, #6A7B8A, #367481);
  color: #fff;
}

.switch-left::before,
.switch-right::before {
  content: "";
  position: absolute;
  width: 0.4em;
  height: 2.45em;
  bottom: -0.45em;
  background-color: #ccc;
  transform: skewY(-65deg);
}

.switch-left::before {
  left: -0.4em;
}

.switch-right::before {
  right: -0.375em;
  background-color: transparent;
  transform: skewY(65deg);
}

input:checked + .switch-left {
  background-color: #0084d0;
  color: #fff;
  bottom: 0px;
  left: 0.5em;
  height: 2.5em;
  width: 3em;
  transform: rotate(0deg) skewX(0deg);
}

input:checked + .switch-left::before {
  background-color: transparent;
  width: 3.0833em;
}

input:checked + .switch-left + .switch-right {
  background-color: #fff;
  color: #888;
  bottom: 0.4em;
  right: 0.8em;
  height: 2.4em;
  width: 2.75em;
  transform: rotate(-15deg) skewX(-15deg);
}

input:checked + .switch-left + .switch-right::before {
  background: #ccc;
}




/* BANK SWITCH */

.toggle {
	 --line: #505162;
	 --dot: #f7f8ff;
	 --circle: #9ea0be;
	 --duration: 0.3s;
	 --text: #9ea0be;
	 cursor: pointer;
}
 .toggle input {
	 display: none;
}
 .toggle input + div {
	 position: relative;
}
 .toggle input + div:before, .toggle input + div:after {
	 --s: 1;
	 content: '';
	 position: absolute;
	 height: 4px;
	 top: 10px;
	 width: 24px;
	 background: var(--line);
	 transform: scaleX(var(--s));
	 transition: transform var(--duration) ease;
}
 .toggle input + div:before {
	 --s: 0;
	 left: 0;
	 transform-origin: 0 50%;
	 border-radius: 2px 0 0 2px;
}
 .toggle input + div:after {
	 left: 28px;
	 transform-origin: 100% 50%;
	 border-radius: 0 2px 2px 0;
}
 .toggle input + div span {
	 padding-left: 0px;
	 line-height: 24px;
	 color: var(--text);
}
 .toggle input + div span:before {
	 --x: 0;
	 --b: var(--circle);
	 --s: 4px;
	 content: '';
	 position: absolute;
	 left: 0;
	 top: 0;
	 width: 24px;
	 height: 24px;
	 border-radius: 50%;
	 box-shadow: inset 0 0 0 var(--s) var(--b);
	 transform: translateX(var(--x));
	 transition: box-shadow var(--duration) ease, transform var(--duration) ease;
}
 .toggle input + div span:not(:empty) {
	 padding-left: 64px;
}
 .toggle input:checked + div:before {
	 --s: 1;
}
 .toggle input:checked + div:after {
	 --s: 0;
}
 .toggle input:checked + div span:before {
	 --x: 28px;
	 --s: 12px;
	 --b: var(--dot);
}
